import {
    useToolKit
} from "@webx-ui/toolkit"

import {
    Button,
    CardBlock
} from "@webx-ui/forms"

import "./stylesheet.scss"

export default function NotFound() {
    const toolkit = useToolKit()

    function MoveToRootButton() {

        function onClick() {
            toolkit.app.goTo(toolkit.app.path)
        }

        return <Button 
                title="  "
                onClick={onClick}
        />
    }

    return <div className="not-found">
        <CardBlock>
            <h1>
                 ?
            </h1>
            <p>
                  ,    .
            </p>
            <p>
                    .
            </p>
            <MoveToRootButton/>
        </CardBlock>
    </div>
}